<template>
    <el-card class="ds-card__module" :id="item.id">
        <p class="ds-p__module_title">{{item.module_name}}</p>
        <el-tabs v-model="activeTab">
            <!--tab-->
            <el-tab-pane v-for="(tabItem, i) in item.tab_list" :label="tabItem.tab_name" :name="tabItem.tab_name">
                <website-area v-bind:website-list="tabItem.website_list" v-bind:options="options"
                              @onMark="handleMark"
                              @onUnmark="handleUnmark"
                              @onItemClick="handleItemClick"/>
                <!--<p>{{item.name}}</p>-->
            </el-tab-pane>
        </el-tabs>
    </el-card>
</template>

<script>
    import WebsiteArea from "~/views/WebsiteArea";

    export default {
        components: {WebsiteArea},
        name: "website-module",
        props: {
            item: {}
        },
        data() {
            const options = [];
            options['mark'] = true;
            options['unmark'] = true;
            return {
                options: options,
                activeTab: ""
            };
        },
        mounted() {
            this.activeTab = this.item.tab_list[0].tab_name;
        },
        methods: {
            handleMark(item) {
                this.$emit('onMark', item);
            },
            handleUnmark(item) {
                this.$emit('onUnmark', item);
            },
            handleItemClick(item) {
                this.$emit('onItemClick', item);
            }
        }
    }
</script>

<style>
    .ds-card__module {
        margin-top: 0px;
        margin-bottom: 40px;
        position: relative;
    }

    .ds-p__module_title {
        text-align: left;
        margin-left: 24px;
        margin-top: 24px;
    }
    .ds-card__module .el-card__body {
        padding-left: 0;
        padding-right: 0;
        padding-top: 0;
    }
    .el-tabs__nav {
        clear: both;
    }
    .el-tabs__nav-scroll {
        transform: translateX(24px);
    }
    .el-tabs__active-bar.is-top {
        margin-left: 0px;
        height: 3px;
    }
    .el-tabs__item {
        font-size: 12px;
        color:#9F9F9F;
        font-weight: bold;
    }
    .el-tabs__nav-wrap::after {
        height: 1px;
    }
</style>


