<template>
    <el-card shadow="hover" class="ds-card__website_item" @click.native="handleItemClick">
        <div class="ds-div__website-item">
            <img class="ds-img__website_item_logo" :src="item.icon"/>
            <span class="ds-span__website_item_title">{{item.name}}</span>
        </div>
        <el-dropdown class="ds-icon__website_item_more" @visible-change="handleDropdownVisibleChange" @command="handleCommand" trigger="click" size="mini" placement="bottom">
            <i class="el-icon-more"></i>
            <!--<img class="ds-dropdown-option" src="../assets/ic_more_normal.png"/>-->
            <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-show="options && options['top']" command="top">置顶</el-dropdown-item>
                <el-dropdown-item v-show="!marked(item) && options && options['mark']" command="mark">收藏</el-dropdown-item>
                <el-dropdown-item v-show="marked(item) && options && options['unmark']" command="unmark">取消收藏</el-dropdown-item>
                <el-dropdown-item v-show="options && options['edit']" command="edit">编辑</el-dropdown-item>
                <el-dropdown-item v-show="options && options['delete']" command="delete">删除</el-dropdown-item>
            </el-dropdown-menu>
        </el-dropdown>
        <p class="ds-p__website_item_desc">{{item.desc}}</p>
    </el-card>
</template>

<script>
    export default {
        name: "website-item",
        props: {
            item:{},
            options: Array
        },
        data() {
            this.optionsHandler = [];
            this.optionsHandler['mark'] = this.handleMark;
            this.optionsHandler['unmark'] = this.handleUnmark;
            this.optionsHandler['top'] = this.handleTop;
            this.optionsHandler['edit'] = this.handleEdit;
            this.optionsHandler['delete'] = this.handleRemove;
            return {
                optionsHandler: this.optionsHandler,
                dropdownVisible: false
            }
        },
        mounted() {
        },
        methods: {
            marked(item) {
                // console.log(item, this.$store.getters.has(item));
                return this.$store.getters.has(item);
            },
            handleCommand(command) {
                console.log("click on command " + command);
                if (this.optionsHandler && this.optionsHandler[command]) {
                    this.optionsHandler[command](this.item)
                }
            },
            handleMark(item) {
                this.$emit('onMark', item);
            },
            handleUnmark(item) {
                this.$emit('onUnmark', item);
            },
            handleRemove(item) {
                // this.$store.getters.remove(item);
                // this.marked = false;
                this.$emit('onDelete', item);
            },
            handleTop(item) {
                this.$emit('onTop', item);
            },
            handleEdit(item) {
                this.$emit('onEdit', item);
            },
            handleItemClick() {
                if (!this.dropdownVisible) {
                    setTimeout(()=> {
                        if (!this.dropdownVisible) {
                            this.$store.getters.visit(this.item);
                            this.$emit('onItemClick', this.item);
                        }
                    }, 10);
                }
            },
            handleDropdownVisibleChange(visible) {
                console.log('dropdownVisible ' + visible);
                this.dropdownVisible = visible;
            }
        }
    }
</script>

<style lang="scss" scoped>
    .ds-div__website-item {
        display: flex;
        display: -webkit-flex;
        -webkit-align-items: center;
        align-items: center;
        -webkit-justify-content: center;
        justify-content: center;
        margin-top: 16px;
    }
    .ds-grid__website_item .el-card.ds-card__website_item.is-hover-shadow {
        background: #FFFFFF;
        border: 1px solid #E4E4E4;
        border-radius: 8px;
        box-shadow: unset;
        -webkit-box-shadow: unset;
    }
    .ds-grid__website_item .el-card.ds-card__website_item.is-hover-shadow:hover {
        background: #EEEFF5;
        border: 1px solid #DBD4E4;
        border-radius: 8px;
        box-shadow: unset;
        -webkit-box-shadow: unset;
        cursor: pointer;
    }
    .ds-img__website_item_logo {
        width: 32px;
        height: 32px;
        display: inline-block;
        margin-left: 16px;
    }
    .ds-card__website_item {
        position: relative;
        height: 97px;
        padding: 0;
    }
    .ds-icon__website_item_more {
        transform: rotateZ(90deg);
        height: 12px;
        position: absolute;
        right: 16px;
        top: 16px;
    }
    .ds-span__website_item_title {
        flex: 1;
        -webkit-flex: 1;
        display: inline;
        text-align: start;
        margin-left: 16px;
        font-size: 14px;
        color: #473858;
        font-weight: bold;
    }
    .ds-p__website_item_desc {
        text-align: start;
        margin-top: 16px;
        margin-bottom: 16px;
        margin-left: 16px;
        color: #6C5F7C;
        font-size: 12px;
    }
    .ds-dropdown-option {
        height: 10px;
    }
    .el-dropdown-menu {
        background: #2D1B43;
        border-radius: 3px;
        .el-dropdown-menu__item {
            color: white;
            font-weight: bold;
        }
        .el-dropdown-menu__item:not(.is-disabled):hover {
            background: #2D1B43;
        }
    }
</style>


