<template>
    <el-dialog id="edit-dialog" class="ds-dialog" :title="dialogTitle" :visible.sync="dialogFormVisible" top="30vh">
        <el-form :model="form" :rules="rules" ref="form" class="ds-form__edit_dialog">
            <p>添加网址</p>
            <el-form-item class="ds-form-item__edit_dialog" prop="url" inline-message="true">
                <el-input v-model="form.url" autocomplete="on" placeholder="网站链接"></el-input>
            </el-form-item>
            <p>添加名称</p>
            <el-form-item class="ds-form-item__edit_dialog" prop="name">
                <el-input v-model="form.name" maxlength="20" autocomplete="on" placeholder="网站名称"></el-input>
            </el-form-item>
            <p>添加说明</p>
            <el-form-item class="ds-form-item__edit_dialog">
                <el-input id="form-desc" maxlength="32" v-model="form.description" autocomplete="on" placeholder="添加该导航的说明"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <span id="cancel" @click="dialogFormVisible = false">取消</span>
            <div id="divider"></div>
            <span id="ok" @click="handleConfirmClicked">{{positiveButton}}</span>
        </div>
    </el-dialog>
</template>

<script>
    export default {
        name: "edit-dialog",
        props: {},
        watch: {
            editingItem: function(val) {
                console.log(val);
                if (val) {
                    this.form = {
                        url: val.url,
                        description: val.desc,
                        name: val.name
                    };
                } else {
                    this.form = {
                        url: '',
                        description: '',
                        name: ''
                    }
                }
            }
        },
        data() {
            return {
                dialogFormVisible: false,
                dialogTitle: '添加导航',
                positiveButton: '确认',
                editingItem: {},
                form: {
                    name: '',
                    description: '',
                    url: ''
                },
                rules: {
                    url: [
                        { validator: this.validateUrl, trigger: 'blur' }
                    ],
                    name: [
                        { validator: this.validateName, trigger: 'blur' }
                    ],
                }
            };
        },
        methods: {
            handleConfirmClicked() {
                // 输入合法，添加
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        console.log('form ', this.form, this.editingItem);
                        this.$emit('onFormSubmit', this.form, this.editingItem);
                        this.dialogFormVisible = false;
                    } else {

                    }
                });
            },
            validateUrl(rule, value, callback) {
                console.log("validating " + rule + "|" + value);
                if (value === '') {
                    callback(new Error('请输入网站地址'));
                } else {
                    callback();
                }
            },
            validateName(rule, value, callback) {
                console.log("validating " + rule + "|" + value);
                if (value === '') {
                    callback(new Error('请输入网站名称'));
                } else {
                    callback();
                }
            },
        },
        mounted() {}
    }
</script>

<style lang="scss">
    @import "../colors.scss";
    #edit-dialog {
        .el-dialog {
            background: #FFFFFF;
            box-shadow: 0 5px 15px 0 rgba(51,43,81,0.08);
            border-radius: 20px;
            width: 400px;
        }
        .el-dialog__headerbtn {
            display: none;
        }
        .el-dialog__header {
            text-align: center;
            padding-top: 24px;
        }
        .el-dialog__title {
            font-size: 16px;
            color: $--color-text-primary;
            letter-spacing: 0;
            font-weight: bold;
        }
        .ds-form__edit_dialog {
            text-align: left;
        }
        .ds-form__edit_dialog input {
            padding: 0;
            border-bottom: 1px solid #dbdbdb;
            border-top:0px;
            border-left:0px;
            border-right:0px;
            border-radius: 0;
        }
        .ds-form__edit_dialog p {
            margin: 0;
        }
        .el-dialog__body {
            padding: 14px 24px;
        }
        .ds-form-item__edit_dialog {
            margin: 12px 0 24px;
        }
        .ds-form-item__edit_dialog p {
            font-size: 12px;
            color: $--color-text-primary;
            letter-spacing: 0.4px;
            font-weight: bold;
        }
        .el-form-item__content {
            line-height: 0;
        }
        .el-input__inner {
            font-size: 12px;
            color: $color-third;
            letter-spacing: 0;
            height: 20px;
            line-height: 20px;
        }

        .el-input__inner::placeholder {
            font-size: 12px;
            color: $color-hint;
            letter-spacing: 0;
        }
        .dialog-footer {
            display: flex;
            display: -webkit-flex;
            -webkit-align-items: center;
                    align-items: center;
            -webkit-justify-content: center;
                    justify-content: center;
        }
        .el-dialog__footer {
            border-top: 1px solid #d9d9d9;
            background: #FAFAFC;
            padding: 0;
            border-radius: 0 0 20px 20px;
        }
        .dialog-footer span {
            flex: 1;
            text-align: center;
        }
        #divider {
            width: 1px;
            height: 35px;
            opacity: 0.8;
            border-left: 1px solid #D9D9D9;
        }
        #cancel {
            font-size: 16px;
            color: #A197AE;
            letter-spacing: 0;
            cursor: pointer;
            padding: 24px 0;
        }
        #ok {
            opacity: 0.7;
            font-size: 16px;
            color: #DF253B;
            letter-spacing: 0;
            font-weight: bold;
            cursor: pointer;
            padding: 24px 0;
        }
    }
</style>


