import activities from './static/activities'

const MARKED_STORAGE_KEY = "getters";
const SORT_METHOD_KEY = "sort_method";
const VISIT_TIMES_KEY = "visit_times";
export const getters = {
    fetch: (state, getters) => () => {
        return JSON.parse(window.localStorage.getItem(MARKED_STORAGE_KEY)||'{}')
    },
    save: (state, getters) => (items) => {
        window.localStorage.setItem(MARKED_STORAGE_KEY,JSON.stringify(items))
    },
    updateSortMethod: (state, getters) => (method) => {
        window.localStorage.setItem(SORT_METHOD_KEY,method);
    },
    sorters: (state, getters) => () => {
        const methods = [];
        methods["hotFirst"] = (a,b) => b.weight - a.weight;
        methods["newFirst"] = (a,b) => b.createTime - a.createTime;
        methods["frequencyFirst"] = (a,b) => getters.getVisitTimes(b) - getters.getVisitTimes(a);
        return methods;
    },
    getSortMethod: (state, getters) => () => {
        const sorter = window.localStorage.getItem(SORT_METHOD_KEY) || "hotFirst";
        return getters.sorters()[sorter];
    },
    getSortMethodKey: (state, getters) => () => {
        return window.localStorage.getItem(SORT_METHOD_KEY) || "hotFirst";
    },
    visit: (state, getters) => (item) => {
        const current = JSON.parse(window.localStorage.getItem(VISIT_TIMES_KEY)) || {};
        const key = getters.keyOf(item);

        const times = current[key] || 0;
        current[key] = times + 1;
        window.localStorage.setItem(VISIT_TIMES_KEY, JSON.stringify(current));
    },
    getVisitTimes: (state, getters) => (item) => {
        const storage = JSON.parse(window.localStorage.getItem(VISIT_TIMES_KEY)) || {};
        return storage[getters.keyOf(item)] || 0;
    },
    addOrReplace: (state, getters) => (item) => {
        if (item.id) {
            getters.update(item);
        } else {
            getters.add(item);
        }
    },
    update: (state, getters) => (item) => {
        const current = getters.fetch();
        const key = getters.keyOf(item);
        if (current[key]) {
            current[key] = item;
        }
        getters.save(current);
    },
    add: (state, getters) => (item) => {
        const current = getters.fetch();
        const key = getters.keyOf(item);
        item.markTime = (new Date()).getTime();
        item.topWeight = (new Date()).getTime();
        current[key] = item;
        getters.save(current);
    },
    remove: (state, getters) => (item) => {
        const current = getters.fetch();
        const key = getters.keyOf(item);
        delete current[key];
        getters.save(current);
    },
    has: (state, getters) => (item) => {
        const key = getters.keyOf(item);
        return getters.hasKey(key);
    },
    hasKey: (state, getters) => (key) => {
        const current = getters.fetch();
        // console.log(current, current[key]);
        return current[key] !== undefined
    },
    keyOf: (state, getters) => (item) => {
        return item.id;
    },
    top: (state, getters) => (item) => {
        const current = getters.fetch();
        const key = getters.keyOf(item);
        item.topWeight = (new Date()).getTime();
        current[key] = item;
        getters.save(current);
    },
    getActivities: (state, getters) => () => {
        return activities
    }
};


