<template>
    <div>
        <search-bar/>
        <sort-options class="ds-radio__sort_options" @onChanged="handleSortChange"/>
        <div v-for="(item, index) in data.module_list">
            <!--module-->
            <website-module v-bind:item="item"
                            @onMark="handleMark"
                            @onUnmark="handleUnmark"
                            @onItemClick="handleItemClick"/>
        </div>
    </div>
</template>

<script>
    import WebsiteArea from "~/views/WebsiteArea";
    import axios from 'axios'
    import WebsiteModule from "~/views/WebsiteModule";
    import SearchBar from "~/views/SearchBar";
    import SortOptions from "~/views/SortOptions";

    export default {
        name: "design-nav",
        components: {
            SortOptions,
            SearchBar,
            WebsiteModule,
            WebsiteArea
        },
        data() {
            return {
                activeTab: 'first',
                data: {
                    name: "假数据"
                },
            }
        },
        mounted() {
            this.loadData();
        },
        created() {},
        methods: {
            getSortMethod() {
                return this.$store.getters.getSortMethod();
            },
            loadData() {
                const data = JSON.parse(JSON.stringify(this.$store.state.design_nav));
                const sortMethod = this.getSortMethod();
                for (const i in data.module_list) {
                    const module = data.module_list[i];
                    for (const j in module.tab_list) {
                        const tab = module.tab_list[j];
                        tab.website_list = tab.website_list.sort(sortMethod);
                    }
                }
                data.module_list = data.module_list.sort((a,b) => b.weight - a.weight);
                this.data = data;
            },
            handleMark(item) {
                console.log('handle add at design nav');
                this.$store.getters.add(item);
                this.loadData();
            },
            handleUnmark(item) {
                console.log('handle unmark at design nav');
                this.$store.getters.remove(item);
                this.loadData();
            },
            handleSortChange(newVal) {
                console.log('handle handleSortChange at free commercial');
                this.loadData();
            },
            handleItemClick(item) {
                console.log('handleItemClick at free commercial', item);
                window.open(item.url);
            }
        }
    }
</script>

<style>
</style>


