<template>
    <div id="create-nav">
        <div id="content-container">
            <img id="icon" src="../assets/add_nav_placeholder.png"/>
            <p id="tip">无需登录，即刻创建我的导航</p>
            <el-button id="button" @click="handleCreateNav">创建我的导航</el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "create-nav",
        methods: {
            handleCreateNav() {
                console.log("handleCreateNav");
                this.$emit('onAddClicked')
            }
        }
    }
</script>

<style lang="scss" scoped>
    #create-nav {
        height: 100%;
        display: flex;
        display: -webkit-flex;
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: center;
                justify-content: center;

        #content-container {
            vertical-align: middle;
            text-align: center;
            /*margin-top: 5%;*/
            #icon {
                height: 240px;
            }
            #tip {
                margin-top: 42px;
                margin-bottom: 0;
                font-size: 18px;
                color: #393850;
                letter-spacing: 0;
                font-weight: bold;
            }
            #button {
                margin-top: 64px;
                background-image: linear-gradient(45deg, rgba(252, 55, 103, 0.04), rgba(253, 54, 111, 0.04));
                border: 1px solid #FB3449;
                border-radius: 25px;
                font-size: 15px;
                color: #FB3449;
                letter-spacing: 0;
                line-height: 13px;
                padding: 16px 56px;
            }
        }
    }
</style>


