<template>
    <div>
        <div id="home">
            <search-bar/>
            <!--module-->
            <el-card class="ds-card__module">
                <p class="ds-p__module_title" v-if="customWebsiteList && customWebsiteList.length > 0">我的导航</p>
                <el-tabs v-model="activeTab" v-if="customWebsiteList && customWebsiteList.length > 0">
                    <el-tab-pane label="自定义导航" name="first">
                        <website-area v-bind:website-list="customWebsiteList" v-bind:show-add="true"
                                      @onAddNavClicked="handleAddClicked"
                                      @onTop="handleTop"
                                      @onEdit="handleEdit"
                                      @onDelete="handleDelete"
                                      @onItemClick="handleItemClick"
                                      v-bind:options="customOptions"/>
                        <!--<p>{{item.name}}</p>-->
                    </el-tab-pane>
                    <el-tab-pane label="我的收藏" name="second">
                        <website-area v-bind:website-list="markedWebsiteList" v-bind:show-add="false"
                                      @onAddNavClicked="handleAddClicked"
                                      @onTop="handleTop"
                                      @onEdit="handleEdit"
                                      @onDelete="handleDelete"
                                      @onItemClick="handleItemClick"
                                      v-bind:options="markedOptions"/>
                        <!--<p>{{item.name}}</p>-->
                    </el-tab-pane>
                </el-tabs>
                <create-nav v-else @onAddClicked="handleAddClicked"></create-nav>
                <edit-dialog ref="nav_add_dialog"
                             @onFormSubmit="handleFormSubmit"></edit-dialog>
            </el-card>
        </div>
    </div>
</template>

<script>
    import WebsiteArea from "~/views/WebsiteArea";
    import WebsiteModule from "~/views/WebsiteModule";
    import SearchBar from "~/views/SearchBar";
    import CreateNav from "~/views/CreateNav";
    import EditDialog from "~/views/EditDialog";

    export default {
        name: "home",
        components: {
            EditDialog,
            CreateNav,
            SearchBar,
            WebsiteModule,
            WebsiteArea
        },
        data() {
            const customOptions = [];
            customOptions['top'] = true;
            customOptions['edit'] = true;
            customOptions['delete'] = true;
            const markedOptions = [];
            markedOptions['top'] = true;
            markedOptions['unmark'] = true;
            return {
                activeTab: 'first',
                data: {
                },
                customWebsiteList:[],
                markedWebsiteList:[],
                customOptions: customOptions,
                markedOptions: markedOptions,
            }
        },
        mounted() {
            this.loadData();
        },
        methods: {
            showCreateNav() {
                this.dialogData().dialogFormVisible = true;
            },
            dialogData() {
                return this.$refs.nav_add_dialog.$data;
            },
            handleAddClicked() {
                delete this.dialogData().editingItem;
                this.dialogData().form = {};
                this.dialogData().dialogTitle = '添加导航';
                this.dialogData().positiveButton = '确认';
                this.showCreateNav();
            },
            handleFormSubmit(form, editingItem) {
                console.log('handleFormSubmit', form, editingItem);
                if (editingItem) {
                    editingItem.url = form.url;
                    editingItem.name = form.name;
                    editingItem.desc = form.description;
                    if (editingItem.source === 'custom') {
                        const a = document.createElement('a');
                        a.href = form.url;
                        console.log('url ' + a);
                        editingItem.icon = a.protocol + "//" + a.hostname + "/favicon.ico";
                    }
                    this.$store.getters.update(editingItem);
                } else {
                    const a = document.createElement('a');
                    a.href = form.url;
                    console.log('url ' + a);
                    const favicon = a.protocol + "//" + a.hostname + "/favicon.ico";
                    const item = {
                        id: (new Date()).getTime(),
                        source: "custom",
                        url: form.url,
                        name: form.name,
                        desc: form.description,
                        icon: favicon
                    };
                    this.$store.getters.add(item);
                }
                this.loadData();
            },
            loadData() {
                const data = this.$store.getters.fetch();
                this.customWebsiteList = [];
                this.markedWebsiteList = [];
                for (const k in data) {
                    console.log('k ' + k + "|" + JSON.stringify(data[k]));
                    if (data[k].source === 'custom') {
                        this.customWebsiteList.push(data[k]);
                    } else {
                        this.markedWebsiteList.push(data[k]);
                    }
                }
                this.customWebsiteList = this.customWebsiteList.sort((a,b)=> b.topWeight-a.topWeight);
                this.markedWebsiteList = this.markedWebsiteList.sort((a,b)=> b.topWeight-a.topWeight);
                console.log("custom", this.customWebsiteList);
                console.log("marked", this.markedWebsiteList);
            },
            handleTop(item) {
                console.log('handle top at home');
                this.$store.getters.top(item);
                this.loadData();
            },
            handleEdit(item) {
                console.log('handle edit at home ' + JSON.stringify(item));
                this.dialogData().editingItem = item;
                this.dialogData().dialogTitle = '编辑导航';
                this.dialogData().positiveButton = '编辑';
                this.showCreateNav();
            },
            handleDelete(item) {
                console.log('handle delete at home');
                this.$store.getters.remove(item);
                this.loadData();
            },
            handleItemClick(item) {
                console.log('handleItemClick at free commercial', item);
                window.open(item.url);
            }
        }
    }
</script>

<style>
    #home .ds-card__module {
        margin-top: 40px;
        height: 80vh;
        min-height: 512px;
    }

    .ds-p__module_title {
        text-align: left;
        margin-left: 24px;
        margin-top: 24px;
    }

    .ds-card__module .el-card__body {
        padding-left: 0;
        padding-right: 0;
        padding-top: 0;
        height: 100%;
    }
    .el-tabs__nav-scroll {
        transform: translateX(24px);
    }
    .el-tabs__active-bar.is-top {
        margin-left: 0px;
    }
    .el-tabs__item {
        font-size: 12px;
        color:#9F9F9F;
        font-weight: bold;
    }
    .el-tabs__nav-wrap::after {
        height: 1px;
    }
</style>


