<template>
    <el-card class="ds-card__search">
        <el-form :inline="true" :model="formInline" class="ds-form">
            <el-form-item id="left" class="ds-form-item">
                <el-input class="ds-input__search" @keyup.enter.native="handleSearch" v-model="formInline.content" postfix-icon="el-icon-search"
                          placeholder="搜索感兴趣的..."></el-input>
            </el-form-item>
            <el-form-item id="right" v-show="true">
                <!--<img class="ds-img__search_icon" @click="handleSearch" src="../assets/ic_search_normal.png"/>-->
                <i class="el-icon-search ds-img__search_icon" @click="handleSearch" ></i>
                <span class="ds-span__divider"></span>
                <el-select v-model="formInline.searchEngine" size="mini">
                    <el-option label="Dribbble" value="https://dribbble.com/search?q="></el-option>
                    <el-option label="Behance" value="https://www.behance.net/search?search="></el-option>
                    <el-option label="Pinterest" value="https://www.pinterest.com/search/pins?q="></el-option>
                    <el-option label="Iconfont" value="https://www.iconfont.cn/search/index?searchType=icon&q="></el-option>
                    <el-option label="花瓣网" value="https://huaban.com/search?q="></el-option>
                </el-select>
            </el-form-item>
        </el-form>
    </el-card>
</template>

<script>
    export default {
        name: "search-bar",
        data() {
            return {
                formInline: {
                    content: '',
                    searchEngine: 'https://dribbble.com/search?q='
                }
            }
        },
        methods: {
            handleSearch() {
                console.log(this.formInline);
                const searchUrl = this.formInline.searchEngine + this.formInline.content;
                if (this.formInline.content && searchUrl) {
                    window.open(searchUrl);
                }
            }
        }
    }
</script>

<style lang="scss">
    $color-secondary: #393850;
    $color-third: #6C5F7C;

    .ds-card__search #left {
        width: 90%;
        margin-bottom: 0pt;
        float: left;
    }

    .ds-card__search #right {
        position: absolute;
        right: 0;
        float: right;
        font-size: 16px;
    }

    .ds-form {
        text-align: left;
        position: relative;
    }

    .ds-form-item {
        margin-bottom: 0;
        text-align: start;
    }

    .ds-card__search .el-card__body {
        padding: 0;
        position: relative;
    }

    .ds-input__search {
        border: 0;
    }

    .ds-card__search #left .el-input {
        display: block;
        border: 0;
        float: left;
    }
    .ds-card__search #left .el-form-item__content {
        width: 100%;
    }

    .ds-card__search .el-select {
        float: right;
        width: 110px;
        color: $color-secondary;
        font-size: 16px;
        font-weight: bold;
    }

    .ds-card__search .el-input__inner {
        border: 0;
        height: 64px;
        line-height: 64px;
    }
    .ds-card__search #left .el-input__inner {
        padding-left: 24px;
        font-size: 16px;
        color: #9F9F9F;
        letter-spacing: 0;
        font-weight: bold;
    }
    .ds-card__search .el-select .el-input__inner {
        font-size: 16px;
        color: $color-secondary;
    }

    .ds-img__search_icon {
        width: 16px;
        vertical-align: middle;
        margin-right: 16px;
        cursor: pointer;
        height: 64px;
        line-height: 64px;
    }

    .ds-span__divider {
        border-left: 1px solid #DBD4E4;
        height: 16px;
    }

    .ds-span__search_engine {
    }
    .el-popper[x-placement^="bottom"] {
        margin-top: 8px;
    }

    .popper__arrow {
        opacity: 0;
    }
    .el-popper {
        background: #FFFFFF;
        border: 1px solid #D9D9D9;
        box-shadow: 0 5px 15px 0 rgba(51,43,81,0.08);
        border-radius: 8px;
    }
</style>


