<template>
    <el-aside style="width: auto; background-color: rgb(255,255,255)">
        <div id="aside-container">
            <div id="home-container">
                <img id="logo" @click="handleLogoClick" height="24px" :src="logo"/>
                <div id="sidebar-action-container" height="24px" @click="handleCollapse">
                    <img id="sidebar-collapse" v-if="!isCollapse" src="../assets/ic_nav_open_normal.png"/>
                    <img id="sidebar-open" v-else src="../assets/ic_nav_close_normal.png"/>
                </div>
            </div>

            <el-menu id="menu" ref="menu" default-active="free-commercial" class="ds-menu-vertical" @open="handleOpen" @close="handleClose"
                     @select="onSelect" :collapse="isCollapse" :collapse-transition="false" :default-openeds="['design-nav']">

                <el-menu-item id="my-nav" :class="{'ds-menu-selected': 'my-nav' === selectedItem}" index="my-nav">
                    <!--<i class="el-icon-menu"></i>-->
                    <img class="ds-menu__nav-icon" :src="myNavItemIcon"/>
                    <span slot="title">我的导航</span>
                </el-menu-item>
                <el-menu-item id="free-commercial" index="free-commercial">
                    <!--<i class="el-icon-document"></i>-->
                    <img class="ds-menu__nav-icon" :src="freeCommercialItemIcon"/>
                    <span slot="title">免费商用</span>
                </el-menu-item>
                <el-submenu id="design-nav" ref="designnNav" @click.native="handleDesignNavClick" index="design-nav" class="el-submenu__parent">
                    <template slot="title">
                        <!--<i class="el-icon-location"></i>-->
                        <img class="ds-menu__nav-icon" :src="designNavItemIcon"/>
                        <span slot="title">设计导航</span>
                    </template>
                    <el-menu-item v-for="(item, index) in this.$store.state.design_nav.module_list" :index="'design-nav#' + item.id">
                        <!--<i class="el-icon-menu" style="color: transparent;"></i>-->
                        <span slot="title">{{item.module_name}}</span>
                    </el-menu-item>
                </el-submenu>
                <el-menu-item id="activity" index="activity">
                    <!--<i class="el-icon-setting"></i>-->
                    <img class="ds-menu__nav-icon" :src="activityItemIcon"/>
                    <span slot="title">线下活动</span>
                </el-menu-item>
                <el-menu-item id="about" index="about">
                    <!--<i class="el-icon-setting"></i>-->
                    <img class="ds-menu__nav-icon" :src="aboutItemIcon"/>
                    <span slot="title">关于我们</span>
                </el-menu-item>
            </el-menu>
        </div>
    </el-aside>

</template>

<style lang="scss">
    @import '../element-variables.scss';
    header {
        background-color: transparent;
        color: #333;
        /*line-height: 60px;*/
    }

    @mixin active-menu {
        color: white;
        width: 176px;
        margin-right: 20px;
        padding-left: 20px;
        background-image: linear-gradient(45deg, #DF2339 0%, #DF253B 100%);
        /*box-shadow: 0 2px 4px 0 rgba(255, 255, 255, 0.50), 0 4px 10px 0 rgba(251, 52, 73, 0.10);*/
        border-radius: 0px 25px 25px 0px;
    }

    #aside-container {
        color: #333;
        height: 100%;
        background-color: #ffffff;
    }

    .el-main {
        padding-left: 40px;
        padding-right: 40px;
        padding-top: 32px;
    }
    .ds-menu-vertical {
        border-right: 0;
    }
    .ds-menu-vertical.el-menu--collapse {
        width: 88px;
    }
    .ds-menu-vertical:not(.el-menu--collapse) {
        width: 200px;
        min-height: 400px;
    }
    .ds-menu__nav-icon {
        width: 16px;
        height: 16px;
    }

    html, body {
        height: 100%;
        margin: 0;
        background-color: #EEEFF5;
    }

    .el-submenu__title:focus, .el-submenu__title:hover, .el-menu-item:focus, .el-menu-item:hover {
        background-color: transparent;
        color: $--color-primary;
    }

    .el-submenu__parent .el-icon-arrow-down {
        color: transparent;
    }
    .el-menu-item {
        height: 48px;
        line-height: 48px;
        font-size: 16px;
        margin-top: 16px;
        margin-bottom: 16px;
        padding-left: 24px;
    }
    .ds-menu-vertical > .el-menu-item.is-active {
        @include active-menu;
        width: 64px;
    }
    .ds-menu-vertical:not(.el-menu--collapse) > .el-menu-item.is-active {
        @include active-menu
    }
    .el-menu-item span {
        font-size: 16px;
        margin-left: 20px;
        color: #393850;
        font-weight: bold;
    }
    .el-menu-item.is-active span {
        color: #fff;
    }
    .el-menu-item img {
        margin-left: 4px;
    }
    .el-submenu__title {
        height: 48px;
        line-height: 48px;
        font-size: 16px;
        margin-top: 16px;
        padding-left: 24px;
    }
    .el-submenu__title span {
        margin-left: 20px;
        font-weight: bold;
    }
    .el-submenu__title img {
        margin-left: 4px;
    }

    .el-submenu__parent .el-menu-item {
        height: 28px;
        line-height: 28px;
    }
    .el-submenu__parent .el-menu-item:first-child {
        margin-top: 8px;
    }
    .el-submenu__parent .el-menu-item:last-child {
        margin-bottom: 8px;
    }

    .el-submenu__parent.is-active .el-submenu__title {
        @include active-menu;
    }
    .el-menu--collapse .el-submenu__parent.is-active .el-submenu__title {
        @include active-menu;
        width: 64px;
    }
    .el-submenu__parent .el-menu-item span {
        font-size: 14px;
        margin-left: 25px;
        font-weight: normal;
    }
    .el-submenu__parent .el-menu-item.is-active span {
        color: $--color-primary;
    }
    .ds-menu-selected {

    }
    #home-container {
        position: relative;
        height: 24px;
        margin-top: 36px;
        padding-left: 24px;
    }
    #sidebar-action-container {
        display: inline-block;
        position: absolute;
        right: 24px;
        cursor: pointer;
    }
    #sidebar-collapse {
        height: 10px;
        margin-top: 7px;
    }
    #sidebar-open {
        height: 10px;
        margin-top: 7px;
    }
    #menu {
        margin-top: 24px;
    }
    #logo {
        cursor: pointer;
    }
    #design-nav:hover {
        cursor: pointer;
    }
    .el-menu--vertical .el-menu {
        display: none;
    }
    .el-tooltip__popper {
        opacity: 0;
    }
    /* 设置滚动条的样式 */
    ::-webkit-scrollbar {
        width:4px;
    }
    /* 滚动槽 */
    ::-webkit-scrollbar-track {
        border-radius:10px;
    }
    /* 滚动条滑块 */
    ::-webkit-scrollbar-thumb {
        border-radius:10px;
        background: #9F9F9F;
    }
    ::-webkit-scrollbar-thumb:window-inactive {
        background: rgba(255, 0, 0, 0);
    }
</style>

<script>
    import router from '../router'
    import myNavNormal from '../assets/ic_home_normal.png'
    import myNavSelected from '../assets/ic_home_selected.png'
    import freeCommercialNormal from '../assets/ic_free_resource_normal.png'
    import freeCommercialSelected from '../assets/ic_free_resource_selected.png'
    import designNavNormal from '../assets/ic_design_normal.png'
    import designNavSelected from '../assets/ic_design_selected.png'
    import activityNormal from '../assets/ic_activity_normal.png'
    import activitySelected from '../assets/ic_activity_selected.png'
    import aboutNormal from '../assets/ic_about_normal.png'
    import aboutSelected from '../assets/ic_about_selected.png'
    import logoOpened from '../assets/logo.png'
    import logoCollapsed from '../assets/logo_small.png'

    export default {
        components: {},
        data() {
            return {
                isCollapse: false,
                selectedItem: 'free-commercial'
            }
        },
        methods: {
            handleCollapse() {
                this.isCollapse = !this.isCollapse;
            },
            handleClick(tab, event) {
                console.log(tab, event);
            },
            handleOpen(key, keyPath) {
                console.log("keyPath " + keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            },
            onSelect(index, indexPath) {
                console.log("index " + index, "indexPath " + indexPath);
                if (index == 0) {
                    this.isCollapse = !this.isCollapse
                } else {
                    router.push(index);
                    let view = document.querySelector("#" + index.split('#')[1]);
                    if (view) view.scrollIntoView();
                    this.selectedItem = index.split('#')[0];
                }
            },
            handleLogoClick() {
                open('/', '_self');
            },
            handleDesignNavClick() {
                console.log('handleDesignNavClick ', this.$refs.designNav);
                this.$refs.designNav.openMenu('design-nav', '');
            }
        },
        computed: {
            myNavItemIcon() {
                return 'my-nav' === this.selectedItem ? myNavSelected : myNavNormal;
            },
            freeCommercialItemIcon() {
                return 'free-commercial' === this.selectedItem ? freeCommercialSelected : freeCommercialNormal;
            },
            designNavItemIcon() {
                return 'design-nav' === this.selectedItem ? designNavSelected : designNavNormal;
            },
            activityItemIcon() {
                return 'activity' === this.selectedItem ? activitySelected : activityNormal;
            },
            aboutItemIcon() {
                return 'about' === this.selectedItem ? aboutSelected : aboutNormal;
            },
            logo() {
                return this.isCollapse ? logoCollapsed : logoOpened;
            }
        }
    };
</script>


