<template>
    <div>
        <div class="wrapper">
            <el-card v-if="showAdd" shadow="hover"  @click.native="handleAddNav" class="ds-grid__website_item ds-card__website_item ds-card__add_nav">
                <div>
                    <img id="add-nav-img" src="../assets/ic_add_nav.png"/>
                    <span id="add-nav-span">添加导航</span>
                </div>
            </el-card>
            <div class="ds-grid__website_item" v-for="(item, index) in websiteList">
                <website-item v-bind:item="item" v-bind:options="options"
                              @onTop="handleTop"
                              @onEdit="handleEdit"
                              @onDelete="handleDelete"
                              @onMark="handleMark"
                              @onUnmark="handleUnmark"
                              @onItemClick="handleItemClick"/>
            </div>
        </div>
    </div>
</template>

<script>
    import WebsiteItem from "~/views/WebsiteItem";

    export default {
        components: {WebsiteItem},
        name: "website-area",
        props: {
            websiteList: {},
            options: Array,
            showAdd: false
        },
        mounted() {
        },
        methods: {
            handleAddNav() {
                console.log("handleAddNav");
                this.$emit('onAddNavClicked');
            },
            handleTop(item) {
                this.$emit('onTop', item);
            },
            handleEdit(item) {
                this.$emit('onEdit', item);
            },
            handleDelete(item) {
                this.$emit('onDelete', item);
            },
            handleUnmark(item) {
                this.$emit('onUnmark', item);
            },
            handleMark(item) {
                this.$emit('onMark', item);
            },
            handleItemClick(item) {
                this.$emit('onItemClick', item);
            }
        }
    }
</script>

<style lang="scss">
    @media screen {
        .wrapper {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            grid-column-gap: 24px;
            grid-row-gap: 24px;
            padding-left: 24px;
            padding-right: 24px;
        }
    }
    @media screen and (max-width: 1439px){
        .wrapper {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            grid-column-gap: 24px;
            grid-row-gap: 24px;
            padding-left: 24px;
            padding-right: 24px;
        }
    }
    .wrapper .el-card__body {
        padding-bottom: 0;
    }
    .el-tabs__header {
        margin: 0 0 32px;
    }

    .ds-card__website_item {
        margin-left: 0em;
        position: relative;
        height: 97px;
        padding: 0;
    }

    .ds-card__add_nav {
        display: flex;
        display: -webkit-flex;
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: center;
                justify-content: center;
        .el-card__body {
            display: flex;
            display: -webkit-flex;
            -webkit-align-items: center;
            align-items: center;

        }
    }
    .ds-grid__website_item.el-card.ds-card__website_item.is-hover-shadow {
        background: #FFFFFF;
        border: 1px solid #E4E4E4;
        border-radius: 8px;
        box-shadow: unset;
        -webkit-box-shadow: unset;
    }
    .ds-grid__website_item.el-card.ds-card__website_item.is-hover-shadow:hover {
        background: #EEEFF5;
        border: 1px solid #DBD4E4;
        border-radius: 8px;
        box-shadow: unset;
        -webkit-box-shadow: unset;
        cursor: pointer;
    }
    #add-nav-img {
        width: 16px;
        height: 16px;
        vertical-align: middle;
    }
    #add-nav-span {
        margin-left: 10px;
    }
</style>


