<template>
    <div>
        <div v-for="(value, key) in activities">
            <p class="ds-activity__title">{{key}}</p>
            <activity-item @click.native="handleClick(item)" class="ds-activity__item" v-for="(item, index) in value" v-bind:item="item"/>
        </div>
    </div>
</template>

<script>
    import ActivityItem from "~/views/ActivityItem";

    export default {
        components: {ActivityItem},
        name: "activity",
        data() {
            return {
                activities: {}
            }
        },
        mounted() {
            this.activities = this.$store.getters.getActivities();
        },
        methods: {
            handleClick(item) {
                window.open(item.url);
            }
        }
    }
</script>

<style>
    .ds-activity__item {
        margin-top: 20px;
        cursor: pointer;
    }
    .ds-activity__item .el-card__body {
        padding-top: 0;
    }
    .ds-activity__title {
        padding-bottom: 8px;
        margin-top: 40px;
        margin-bottom: 32px;
        color: #9F9F9F;
        border-bottom: 1px solid #D9D9D9;
    }
</style>


