<template>
    <el-radio-group id='sortOptions' v-model="radio" @change="handleChange">
        <el-radio label="hotFirst">热门优先</el-radio>
        <el-radio label="newFirst">最新优先</el-radio>
        <el-radio label="frequencyFirst">访问最多</el-radio>
    </el-radio-group>
</template>

<script>
    export default {
        name: "sort-options",
        data () {
            return {
                radio: 'hotFirst'
            };
        },
        mounted() {
            this.radio = this.$store.getters.getSortMethodKey()
        },
        methods: {
            handleChange(val) {
                this.$store.getters.updateSortMethod(val);
                this.$emit('onChanged', val);
            }
        }
    }
</script>

<style lang="scss">
    $color-secondary: #393850;
    $color-third: #6C5F7C;

    #sortOptions {
        margin-bottom: 16px;
    }
    #sortOptions .el-radio {
        margin-right: 24px;
    }
    #sortOptions .el-radio__label {
        font-size: 12px;
        padding-left: 8px;
        color: $color-secondary;
        font-weight: normal;
    }
    #sortOptions .el-radio.is-checked .el-radio__label {
        font-weight: bold;
        color: $color-secondary;
    }
    #sortOptions .el-radio__input.is-checked .el-radio__inner {
        border-color: $color-secondary;
        background: $color-secondary;
    }
    .el-radio__inner:hover {
        border-color: $color-secondary;
    }
</style>


