<template>
    <div id="app">
        <el-container style="height: 100vh; border: 1px solid #eee">
            <Sidebar></Sidebar>
            <el-container>
                <el-header hidden>
                </el-header>

                <el-main>
                    <router-view id="router-view"></router-view>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    import Sidebar from './components/Sidebar.vue'

    export default {
        components: {Sidebar},
        methods: {}
    }
</script>

<style lang="scss">
    #app {
        font-family: Helvetica, sans-serif;
        text-align: left;
    }
    #router-view {
        max-width: 1320px;
        margin-left: auto;
        margin-right: auto;
    }
</style>



