<template>
    <div id="activity-item">
        <el-card class="es-card__activity" v-bind:style="{opacity: end ? 0.4 : 1}">
            <div id="left">
                <img class="activity-icon" src="../assets/logo.png"/>
                <div id="activity_container">
                    <p class="ds-p__activity_title">{{item.title}}</p>
                    <p class="ds-p__activity_desc">{{item.desc}}</p>
                </div>
            </div>
            <div id="right">
                <div style="height: 65px; display: inline-block;border-left: 1px solid #D9D9D9;"></div>
                <div style="width: inherit;flex: none;margin-left: 32px;">
                    <p class="ds-p__activity_time">{{item.startTime}}</p>
                    <p class="ds-p__activity_location">{{item.location}}</p>
                </div>
            </div>
        </el-card>
    </div>
</template>

<script>
    export default {
        name: "activity-item",
        props: {
            item: {}
        },
        data() {
            return {
                end: false
            }
        },
        mounted() {
            const endTime = this.item.endTime;
            if (endTime) {
                let dateStr = endTime + " 23:59:59";
                dateStr = dateStr.replace(/-/g, '/');
                const endTimestamp = new Date(dateStr).getTime();
                this.end = endTimestamp < (new Date()).getTime();
            }
            console.log("end", this.end);
        }
    }
</script>

<style lang="scss">
    @import '../colors';

    #activity-item {
        .es-card__activity {
            height: 97px;
            background: #FFFFFF;
            box-shadow: 0 5px 15px 0 rgba(51,43,81,0.08);
            border-radius: 8px;
        }
        .es-card__activity:hover {
            box-shadow: 0 5px 15px 0 rgba(176,186,204,0.6);
        }
        .el-card__body {
            padding: 0;
        }

        #left {
            height: 97px;
            float: left;
            display: flex;
            display: -webkit-flex;
            -webkit-align-items: center;
            align-items: center;
        }

        #right {
            width: 193px;
            height: 97px;
            float: right;
            display: flex;
            display: -webkit-flex;
            -webkit-align-items: center;
            align-items: center;
            padding-left: 32px;
            padding-right: 32px;
        }

        .activity-icon {
            width: 40px;
            height: 40px;
            float: left;
            vertical-align: middle;
            margin-left: 32px;
        }

        #activity_container {
            margin-left: 16px;
            flex: none;
            -webkit-flex: none;
        }

        .ds-p__activity_title {
            font-size: 14px;
            color: $color-secondary;
            font-weight: bold;
            margin: 0;
        }

        .ds-p__activity_desc {
            font-size: 12px;
            color: $color-third;
            margin-top: 8px;
            margin-bottom: 0;
        }

        .ds-p__activity_time {
            font-weight: bold;
            margin: 0;
            font-size: 14px;
            color: $color-secondary;
        }
        .ds-p__activity_location {
            margin-top: 8px;
            margin-bottom: 0;
            font-size: 12px;
            color: $color-third;
        }
    }
</style>


